.main-info {
  padding: 50px 20px;
  background-color: #fff;
  background-image: url('https://img.freepik.com/free-photo/high-angle-desktop-with-laptop-copy-space_23-2148430882.jpg?w=900&t=st=1718287206~exp=1718287806~hmac=9f328a7daba34d9edd362c4fb157c0fbbab6d8d4de78ec3f2537213df811b65e'); /* Replace with the path to your background image */
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  min-height: 100vh; /* Ensure the section takes up at least the full height of the viewport */
  height: auto; /* Allow the section to grow if needed */
}

.profile-photo {
  height: 500px;
  width: auto;
  margin-bottom: 20px;
  margin-left: 15%;
  border-bottom: 30px solid #000000c0;
}

.profile-photo:hover {
  box-shadow: 0 0 20px rgba(255, 255, 255, 1);
}

.details {
  width: 60%;
  background-color: rgba(10, 10, 10, 0.301);
  padding: 20px;
  border-radius: 10px;
  text-align: center;
  transition: transform 0.3s, box-shadow 0.3s;
}

.details:hover {
  transform: scale(1.05);
  box-shadow: 0 0 20px rgba(255, 255, 255, 0.8);
}

.main-info h2 {
  margin-top: 2px;
  color: #fff;
}

.main-info h1 {
  font-size: 3em;
  margin-bottom: 1vh;
  color: #fff;
}

.main-info h3 {
  color: #fff;
  font-size: 2em;
}

.main-info p {
  font-size: 0.9em;
  color: #fff;
  text-align: justify;
}

.cv {
  color: #fff;
  background-color: #5e5d5d;
  border: none;
  padding: 10px 20px;
  font-size: 1em;
  border-radius: 5px;
  cursor: pointer;
  transition: transform 0.3s, background-color 0.3s;
  margin-top: 20px;
}

.cv:hover {
  transform: scale(1.1);
  background-color: #333;
}

/* Responsive */
@media (min-width: 601px) {
  .main-info {
    flex-direction: row;
    align-items: center;
    text-align: left;
  }

  .profile-photo {
    margin-bottom: 0;
    margin-right: 20px;
  }

  .details {
    margin-right: 20px;
    text-align: left;
  }

  .cv {
    margin: 0 auto; /* Center the button */
  }
}

@media (max-width: 600px) {
  .main-info {
    padding: 30px 10px;
    align-items: center;
    height: auto; /* Allow height to grow as needed */
  }

  .main-info h1 {
    font-size: 1.5em;
  }

  .main-info p {
    font-size: 1em;
  }

  .profile-photo {
    height: 300px;
    width: auto;
    margin-bottom: 0;
    margin-right: 15%;
    margin-left: 15%;
  }

  .details {
    width: 100%; /* Take full width */
    margin-top: 15px;
  }

  .cv {
    width: 80%; /* Adjust button size */
    text-align: center;
   
  }
  .details a{
    text-decoration: none;
  }
  

  
}
